<template>
    <div>
        <van-nav-bar
        title="加入我们"
        />
        <div class="logobox">
            <van-image
            width="100"
            height="100"
            :src="require('./../assets/lovelogo.png')"
            round
            />
        </div>
        <van-cell-group>
            <van-cell title="分享小程序" is-link >
                <template #title>
                
