            <van-cell title="网站" is-link>
                <template #default>
                <span>www.sxl.cn</span>
                </template>
            </van-cell>
        </van-cell-group>
    </div>
</template>
<script>
import { NavBar , Image as VanImage,Cell, CellGroup,Icon } from 'vant';
export default {
    name:'index3',
    components:{
        NavBar,VanImage,
        Cell, CellGroup,
        Icon 
    }
}
</script>
<style lang="">
*{
    padding: 0px;
    margin: 0px;
}
    .van-nav-bar__content{
   background: rgb(239, 84, 126);
 }
 .van-nav-bar__title{
   color: white;
 }
 .logobox{
     text-align: center;
     width: 100%;
     height: 7rem;
     background: rgb(239, 84, 126);
 }

</style>


