<template>
    <div>
        <van-nav-bar
    title="白头翁计划"
    >
    <template #right>
    <van-icon name="weapp-nav" color="white"/>
    </template>
    </van-nav-bar>
        <van-row>
        <van-col span="8">
        <van-sidebar v-model="activeKey" @change="onChange">
        <van-sidebar-item title="全部"  @click="agin"/>
        <van-sidebar-item title="传统公益服务" />
        <van-sidebar-item title="VR技术" />
        <van-sidebar-item title="新媒体征集"/>
        </van-sidebar>
        </van-col>
        <van-col span="16" style="margin-top:20px" v-if="list">
            <van-image
            v-for="(item,index) in list"
            :key="index"
            width="5rem"
            height="5rem"
            :src="require('./../assets/img'+item.index+'.jpg')"
            >
             <template #default >
                 <span style="font-size:11px;margin-right:5px">{{item.title}}</span>
            </template>
            </van-image>
        </van-col>
        </van-row>
    </div>
</template>
<script>
import { NavBar ,Sidebar, SidebarItem,Col, Row,Image as VanImage } from 'vant';
export default {
    name:'index2',
    components:{
        NavBar,Sidebar, SidebarItem,Col,Row,VanImage
    },
    data:function(){
        return{
            activeKey:0,
            list:[
                {title:'传统公益服务',index:3},
                {title:'VR技术',index:2},
                {title:'新媒体征集',index:1},
            ],
        }
    },
    methods:{
        onChange:function(index){
            if(this.list.length>1){
                var i=parseInt(index)
                var nlist=this.list[i-1]
                this.list=[]
                this.list.push(nlist)
            }else{
                this.list=[
                {title:'传统公益服务',index:3},
                {title:'VR技术',index:2},
                {title:'新媒体征集',index:1},
                ];
                var i=parseInt(index)
                var nlist=this.list[i-1]
                this.list=[]
                this.list.push(nlist)
                }
        },
        agin:function(){
            location.reload();

        }
    }
}
</script>
<style lang="">
    .van-nav-bar__content{
   background: rgb(239, 84, 126);
 }
 .van-nav-bar__title{
   color: white;
 }
</style>


